  font-family: "Young Style", sans-serif;
  font-family: "Young Style Underlines", sans-serif;
  src: url("https://fonts.cdnfonts.com/css/young-style") format("woff2");
}
body {
  background-color: #fff7e6;
  margin: 0;
  font-family: Arial, sans-serif;
  overflow-x: hidden; /* Prevent horizontal overflow */
}

.photos {
  max-width: 800px;
}

.product-imgs {
  max-width: 100px;
}

header {
  background-color: #4a3f35;
  color: #fff7e6;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header #home-btn {
  font-family: "Young Style", sans-serif;
  font-size: 20px;
}
header button#menu-btn {
  background-color: transparent;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #fff7e6;
  transition: color 0.3s;
}
header button#menu-btn:hover {
  color: #bcaaa4;
}
header h1 {
  margin: 0;
}
header h1 a {
  color: #fff7e6;
  text-decoration: none;
  transition: color 0.3s;
}
header h1 a:hover {
  color: #bcaaa4;
}

main {
  display: flex;
  justify-content: space-between;
  width: 100%; /* Set width to 100% */
}
main .navigation {
  background-color: #4a3f35;
  color: #fff7e6;
  flex: 1;
  padding: 10px;
  max-width: 100px;
}
main .navigation nav {
  display: flex;
  flex-direction: column;
  gap: 10px;
  min-height: 800px;
}
main .navigation nav a {
  text-decoration: none;
  color: #fff7e6;
  font-weight: bold;
  transition: color 0.3s;
}
main .navigation nav a:hover {
  color: #bcaaa4;
}
main #app {
  flex: 3;
  padding: 20px;
  overflow-y: auto; /* Enable vertical scrolling */
}
main #cart {
  display: flex;
  flex-direction: row;
  height: 40px;
  width: 60px;
  position: fixed;
  bottom: 40px;
  right: 30px;
  background-color: #bcaaa4;
  padding: 20px;
  border-radius: 0.5em;
  border: solid #4a3f35 2px;
  text-decoration: none;
  color: #4a3f35;
